#titulo h1 {
    color: rgb(194, 118, 118);
    font-size: 40px;
    font-weight: bold;
    transition: transform 0.3s;
}

#titulo h1:hover {
    transform: translate(100px, 10px);
}
#productos h2 {
    color: rgba(209, 81, 81, 0.696);
    background-color: rgb(247, 234, 234);
    transition: transform 0.3s;
}

#productos:hover {
    transform: translate(5px, 5px);
}
main {
    display: flex;
    flex: 1;
    width: 2000px;
    flex-direction: row;
    justify-content: space-between;
    height: 600px;
    padding: 0.5em;;
}
#tortas {
    color: rgb(163, 106, 106);
    transition: transform 0.3s;
    animation-duration: 2s;
    animation-name: aparecer;
    animation-iteration-count: infinite;
}   
@keyframes aparecer {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
    0% {
        opacity: 0;
    }
}

#tortas:hover {
    transform: rotate(2deg);
}

