#imagen1 {
    filter: blur(2px);
    filter: grayscale(0.5);
    transition: transform 0.3s;
    position: relative;
    border-radius: 5px;
}

#imagen1:hover {
    transform: scale(1.2);
    z-index: 2;
    cursor: pointer;
    filter: grayscale(0);
    outline: solid 3px;
    filter: blur(0px);
}

