header {
    display: grid;
    grid-area: encabezado;
    background-image: url(https://images.aws.nestle.recipes/original/e89d19d331ac413811e3b1c11a893991_torta-princesa.jpg);
    background-size: 100%;
}

input[type=checkbox] {
    transition: all 0.5s;
}

input[type=checkbox]:checked {
    width: 64px;
    height: 64px;
}

input {
    transition: all 0.5s;
}

input:focus {
    background-color: rgb(210, 225, 239);

}
#main {
    display: grid;
    grid-area: cuerpo;
    grid-template-columns:auto;
    grid-template-rows: auto;
    max-width: 100%;
    background-color: rgba(172, 207, 158, 0.221);
    background-size: 100%;
}